export default {
  data() {
    return {
      oid:'',
      detail:'',
      tid:'',
    };
  },
  onLoad:function(options){
    if(options.oid){
      this.oid = options.oid
    }
    if(options.tid){
      this.tid = options.tid
    }
    this.getDetail()
  },
  methods: {
      getDetail(){
        this.$http({
          aurl:'orderSonDetail',
          params:{
            oid:this.oid
          },
          success:res => {
            console.log(res)
            if(res.data.code===0){
              this.detail = res.data.result
            }
          },
          fail:error => {
            console.log(error)
          }
        })
      }
  },
  mounted() {
  },
};
